@import url(https://fonts.googleapis.com/css2?family=Nanum+Gothic+Coding&family=Noto+Sans+KR&family=Roboto&display=swap);
.Container_Left {
    background-color: #1E90FF;
    position: fixed;

    top: 0;
    left: 0;
    height: 100%;
    width: 40%;
    min-width: 450px;
    display: flex;
    flex-direction: column;

    box-shadow: 4px 0px 4px rgba(0,0,0,.2);
}

.Container_Left .Image{
  text-align: center;
  padding: 130px 0px 40px;
}

.Container_Left .Image img{
    width: 55%;
}

.Container_Left .Description{
    text-align: center;
    color: White;
}

.Container_Left .Description h1{
    margin-bottom: 25px;
    color: White;
}

.Container_Left .Description p{
    margin-bottom: 110px;
    line-height: 30px;
}

.Container_Left .Button{
    background-color: white;
    padding-top: 8px;
    box-shadow: 0 2px 4px 0 #00000033;
    margin-bottom: 35px;
    height: 50px;
    width: 200px;
}

.Container_Left .Button img{ 
    width:22px;
    margin-right: 12px;
}

.Container_Left .Privacy{
    text-align: center;
    font-size: small;
    color: rgb(241, 243, 245);
}

.Container_Left .Privacy a{
    display: inline;
    font-size: small;
    color: rgb(241, 243, 245);
}


.Container_Right {
    height: 100%;
    width: 60%;

    min-width: 500px;
    display: flex;
    flex-direction: column;
    float:right;

    padding-left: 120px;
    padding-right: 120px;
}

.Container_Right .Notice img{
    width: 200px;
    margin-left: 200px;
    margin-top: 40px;
}

.Container_Right .Block{
    display: flex;
    flex-direction: row;
    margin: 10px 10px 10px 10px;
    height: 165px;
}

.Container_Right .Block .Block_Description{
    color:#56565a;
    line-height: 25px;
    min-width: 430px;
    min-width: 300px;
}

.Container_Right .Block .Block_Description p{
    position: relative;
    top: 10px;
}

.Container_Right .Block .Block_Image img{
    margin-right: 60px;
    width: 100px;
}

.Container_Right .Block .Block_Image img:hover{
    cursor: pointer
}




